
:root {
    transition: 1s;
    --main-bg-color-header: #1777d2;
    --main-bg-color-html: #fafafa;
    --main-bg-color-reverse: #fafafad1;
    --main-primary: #448aff;
    --color: #fff;
    --color-negative: #fff
    /*background-color: var( --main-bg-color-reverse) !important;*/
}
.v-card {
    color: black !important;

    background: var(--main-bg-color-reverse) !important;
}
.root-color {
    background-color: var(--main-bg-color-header) !important;
    color: white;
}
.success {
    background-color: #4caf50;
    color: var(--color);
}
.back {
    background-color: var(--main-primary) !important;
    color: var(--color);
}
.v-input--selection-controls__input {
    margin-top: 4px;
    margin-right: 0 !important;
}
.custom-link {
    background: #448aff;
    color: var(--color);
    text-decoration: none;
}
.main-preloader {
    margin-top: 100px;
    text-align: center;
    /*display: flex;*/
    /*justify-content: center;*/
    /*text-align: center;*/
    /*display: flex;*/
    /*justify-content: center;*/
    /*align-items: c;*/
}
.main-preloader-text {
    color: var(--main-bg-color-header);
    margin-top: 10px;
    font-size: 25px;
    font-weight: 600;
}
.circle {

    /*margin: 0 auto;*/
    width: 100px;
    height: 100px;
    position: relative;
    margin-bottom: 40px;
}
.circle .child {
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0;
    top: 0;
}
.circle .child:before {
    content: "";
    display: block;
    margin: 120px auto;
    width: 25%;
    height: 25%;
    background-color: var(--main-bg-color-header);
    border-radius: 100%;
    animation: circleBounceDelay 1.2s infinite ease-in-out both;
}
.circle .circle2 {
    transform: rotate(30deg);
}
.circle .circle3 {
    transform: rotate(60deg);
}
.circle .circle4 {
    transform: rotate(90deg);
}
.circle .circle5 {
    transform: rotate(120deg);
}
.circle .circle6 {
    transform: rotate(150deg);
}
.circle .circle7 {
    transform: rotate(180deg);
}
.circle .circle8 {
    transform: rotate(210deg);
}
.circle .circle9 {
    transform: rotate(240deg);
}
.circle .circle10 {
    transform: rotate(270deg);
}
.circle .circle11 {
    transform: rotate(300deg);
}
.circle .circle12 {
    transform: rotate(330deg);
}
.circle .circle2:before {
    animation-delay: -1.1s;
}
.circle .circle3:before {
    animation-delay: -1s;
}
.circle .circle4:before {
    animation-delay: -0.9s;
}
.circle .circle5:before {
    animation-delay: -0.8s;
}
.circle .circle6:before {
    animation-delay: -0.7s;
}
.circle .circle7:before {
    animation-delay: -0.6s;
}
.circle .circle8:before {
    animation-delay: -0.5s;
}
.circle .circle9:before {
    animation-delay: -0.4s;
}
.circle .circle10:before {
    animation-delay: -0.3s;
}
.circle .circle11:before {
    animation-delay: -0.2s;
}
.circle .circle12:before {
    animation-delay: -0.1s;
}
@-webkit-keyframes circleBounceDelay {
0%,
    80%,
    100% {
        transform: scale(0);
}
40% {
        transform: scale(1);
}
}
@keyframes circleBounceDelay {
0%,
    80%,
    100% {
        transform: scale(0);
}
40% {
        transform: scale(1);
}
}
@-webkit-keyframes button-jump {
0% {
        box-shadow: 0px 2px 5px #555;
}
50% {
        -webkit-transform: scale(1.1);
        transform: scale(1.1);
        box-shadow: 0px 6px 10px #555;
}
}
.v-text-field .v-input__slot:before {
    background-color: grey;
}
.no-shadow {
    box-shadow: none !important;
}
.header-btn {
    min-width: 25px !important;
    padding: 0 5px !important;
    margin: 0 !important;
}
.v-stepper__header .v-divider {

    background: grey;
}
.v-card {
    padding: 1% 2%;
}
.logo {
    color: #fff
}
button:focus {
    outline: none;
}
html {
    overflow-y: auto !important;
}
html,
body {
    transition: 1s;

    height: 100%;
    margin: 0;
    padding: 0;
    background: var(--main-bg-color-html);
    color: var(--color-negative);
}
header {
    transition: 1s;

    height: 60px;
    width: 100%;
    background: var(--main-bg-color-header);
    display: flex !important;
    align-items: center;
    padding: 0 20px;
    color: var(--color);
    font-size: 22px;
}
.wrapper {
    display: flex;
    flex-direction: column;
    height: 100%;
}
.content {
    flex: 1 0 auto;
}
.footer {
    flex: 0 0 auto;
}
.info-token {
    border-top: 1px solid black;
    height: 50px;
    background: var(--main-bg-color-header);
    padding: 0 3%;
    display: flex;
    align-items: center;
    color: var(--color);
}
.header-txt {
    display: block;
}
@media screen and (max-width: 370px) {
.header-txt {
        display: none;
}
}





